.container{grid-column:span 12;display:grid;grid-template-columns:repeat(12, 1fr);column-gap:30px;height:500px;width:100%;background-color:#ffecec;position:absolute;box-sizing:border-box;top:100px;z-index:100;padding:60px;border-radius:30px}.form{grid-column:3/11;background-color:#f8f8f8;display:flex;flex-direction:row;flex-direction:column;justify-content:center;border-radius:30px;box-sizing:border-box}.email,.password,.enter{margin:15px 45px;text-align:center;border-radius:10px;box-sizing:border-box}.email,.password{padding:20px 0;border:2px solid #afb5c0}.close{background-color:rgba(0,0,0,0);width:30px;height:30px;position:absolute;top:20px;right:20px;border:none;cursor:pointer}.close:before{content:"✖";font-size:20px;color:#f53855}.close:hover:before{color:#fff}@media screen and (max-width: 780px){.form{grid-column:span 12}}@media screen and (max-width: 780px){.container{padding:20px}.close{top:35px;right:35px}}@media screen and (max-width: 420px){.container{padding:10px;column-gap:15px}}