.dropdown{grid-column:span 12;background-color:#f17e8f;position:absolute;top:100px;left:0;width:100%;z-index:100;display:flex;flex-direction:row;flex-direction:column;text-align:center;border-radius:30px;padding-top:28px}.container{width:100%;padding:15px 15px 30px}.close{background-color:rgba(0,0,0,0);width:30px;height:30px;position:absolute;top:20px;right:20px;border:none;cursor:pointer}.close:before{content:"✖";font-size:20px;color:#fff}.close:hover:before{color:#fff}